.wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
}

#sidebar {
    min-width: 200px;
    max-width: 200px;
    background: #7386D5;
    color: #fff;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    height: 100%;
    overflow-y: auto;
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar .sidebar-header {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background: #6d7fcc;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #47748b;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}

#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: #fff;
    background: #6d7fcc;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #6d7fcc;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #fff;
    color: #7386D5;
}

a.article,
a.article:hover {
    background: #6d7fcc !important;
    color: #fff !important;
}

@media (max-width: 768px) {
    /*#sidebar {
        margin-left: -250px;
    }
    #sidebarCollapse span {
        display: none;
    }*/
    #sidebar.active {
        margin-left: 0;
    }
}

.trigger {
    cursor: pointer;
    background: #5b6cb6;
    font-weight: bold;
    color: #000 !important;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}


.active, .trigger:hover {
    background-color: #5664a3;
}

.trigger:after {
    content: '+'; /* plus button to the right*/
    color: #000;
    font-weight: bold;
    float: right;
    margin-left: 5px;
    
}

  .active:after {
    content: "\2212";
}
.selected {
    stroke: yellow;
}

g {
    cursor: pointer;
}
#toolbar {
    min-height: 125px;
    max-height: 125px;
    background: rgb(94, 114, 192);
